import React, { Component } from 'react'
import { Tabs, Spin } from 'antd'
const TabPane = Tabs.TabPane

import { handleLocation } from '/api/tools'
import ProjectForm from '/page/project/projectForm.jsx'
import ProjectUser from '/page/project/projectUser.jsx'
import ProjectMachine from '/page/project/projectMachine.jsx'

import { getprojitemdetail } from '/api/project'

class ProjectIndex extends Component{

	componentWillMount = _ => {
		let id = handleLocation().id
		console.log(id)
		this.setState({id})
		this.search()
	}

	state = {
		active: 1,
		id: undefined,
		loading: false,
		item: {},
		eqs: [],
		mems: []
	}


	search = async _ => {
		await this.setState({loading: true})
		return getprojitemdetail({id: this.state.id})
		.then(res => {
			console.log(res.data)
			this.setState({
				loading: false,
				item: res.data.item,
				eqs: res.data.eqs,
				mems: res.data.mems
			})
		})
	}

	saveDone = id => this.setState({id})

	render = _ => <div style={{padding: '0 15px', height: '100%'}}>
	<Spin spinning={this.state.loading}>
		<Tabs 
		className={'goodTabs'}
		defaultActiveKey="1">
			<TabPane tab="基本信息" key="1">
				<div style={{paddingBottom: 76}}>
					<ProjectForm id={this.state.id} saveDone={this.saveDone} item={this.state.item} />
				</div>
			</TabPane>
			<TabPane tab="项目组成员" key="2">
				<div style={{paddingBottom: 76}}>
					<ProjectUser id={this.state.id} saveDone={this.saveDone} mems={this.state.mems} />
				</div>
			</TabPane>
			<TabPane tab="项目涉及的设备" key="3">
				<div style={{paddingBottom: 76}}>
					<ProjectMachine id={this.state.id} saveDone={this.saveDone} eqs={this.state.eqs} />
				</div>
			</TabPane>
		</Tabs>
	</Spin>	
	</div>
}

export default ProjectIndex


